	import helper from '@/common/js/helper.js'

	export default {
		data() {
			return {
			}
		},
		onShow() {
helper.initSession()
		},
		methods: {
			onLogout(){
				helper.ajax.post('/v1/clerk/logout',{},res=>{
					if(res.status===200){
						helper.delSession()
						uni.reLaunch({
							url:'../../login/login'
						})
						return
					}
					console.log(res.message);
				})
			}
		}
	}
